const DRAWER = 'DRAWER'

function drawerCha (value) {
    return {
        type: DRAWER,
        value
    }
}

export function drawerChange (value) {
    return {
        type: DRAWER,
        value
    }
}

export default function sidebar (state=null, action) {
    switch(action.type) {
        case DRAWER :
            console.log(state, action.value.test)
            return Object.assign({}, state, action.value.test)
        default :
            return state
    }
}